import React, { useState } from 'react';
import Papa from 'papaparse';

const CSVReader = ({ onDataLoad, questions }) => {
  const [isLoading, setIsLoading] = useState(false);  // 

  const handleFileUpload = (event) => {
    if (questions.length > 0) {
      // 
      if (!window.confirm('')) {
        return;  // 
      }
    }
    const file = event.target.files[0];
    if (file && file.name.endsWith('.csv')) {
      setIsLoading(true);  // 

      const reader = new FileReader();
      reader.onload = (e) => {
        console.log(file);
        loadCSV(e.target.result, file.name);
      };        
      reader.readAsText(file);
    }
  };

  const loadCSV = (csvData, fileName) => {
    const parsedData = Papa.parse(csvData, { header: true, skipEmptyLines: true });

    const newQuestions = parsedData.data.map((row) => ({
      problem: row[''],
      options: [
        row['1'],
        row['2'],
        row['3'] || '',
        row['4'] || '',
        row['5'] || '',
      ].filter((opt) => opt),
      answer: row[''].split(',').map((n) => parseInt(n, 10)),
      explanation: row[''],
      attempts: parseInt(row[''], 10),
      correctCount: parseInt(row[''], 10),
      priority: parseFloat(row['']),
      gap: parseInt(row[''], 10),
      type: row['']
    }));

    onDataLoad(newQuestions, fileName); // 
    setIsLoading(false);  // 
  };

  return (
    <div className="container mt-4">
      <div className="card p-4 shadow-sm">
        <h5 className="mb-3">CSV</h5>
        <p className="text-muted mb-4">
          CSV
        </p>

        <div className="d-flex justify-content-center mb-3">
          <input
            type="file"
            accept=".csv"
            onChange={handleFileUpload}
            className="form-control"
            disabled={isLoading} // 
          />
        </div>
      </div>
    </div>
  );
};

export default CSVReader;
